<template>
  <div id="app" align="center">
    <h1>Word Translator</h1>
    <h5>Powered by Vue.js</h5>
    <translateForm @formSubmit="translateText"></translateForm>
    <translateOutput v-text="translatedText"></translateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'
 
export default {
  name: 'App',
  components: {
    'translateForm': TranslateForm,
    'translateOutput': TranslateOutput
  },
  data() {
    return {
      translatedText: ''
    }
  },
  methods: {
    translateText: function(text, lang) {
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180217T090016Z.fd09d9925d8f9162.a7060d7bb5a237da3d7ed6fc9cefffd2d75349a8&lang='+lang+'&text=' + text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      })
    }
  }
}
</script>

<style>
body {
  background: #fefefe;
}
</style>



